/**
 * Categoras unificadas para toda la aplicacin
 * Estas categoras estn sincronizadas con el backend
 */

// Categoras de EGRESOS
export const CATEGORIAS_EGRESO = [
  "Alimentos y Bebidas",
  "Transporte",
  "Vivienda",
  "Servicios Bsicos",
  "Ocio y Entretenimiento",
  "Compras Personales",
  "Salud",
  "Educacin",
  "Impuestos y Tasas",
  "Servicios Financieros",
  "Compras de Negocio",
  "Otros Egresos",
];

// Categoras de INGRESOS
export const CATEGORIAS_INGRESO = [
  "Ventas de Productos",
  "Prestacin de Servicios",
  "Cobranzas",
  "Transferencias Recibidas",
  "Inversiones y Rendimientos",
  "Otros Ingresos",
];

// Todas las categoras (para uso genrico)
export const TODAS_LAS_CATEGORIAS = [
  ...CATEGORIAS_EGRESO,
  ...CATEGORIAS_INGRESO,
];

/**
 * Obtiene las categoras segn el tipo de registro
 * @param {string} tipo - 'Ingreso', 'Egreso', o null para todas
 * @returns {string[]} Array de categoras
 */
export function obtenerCategorias(tipo) {
  if (!tipo) return TODAS_LAS_CATEGORIAS;
  if (tipo === "Ingreso") return CATEGORIAS_INGRESO;
  if (tipo === "Egreso") return CATEGORIAS_EGRESO;
  return TODAS_LAS_CATEGORIAS;
}
